/* Nav */
*{
  margin: 0;
  box-sizing:border-box;
}
.header {
  padding: 30px;
  text-align: center;
}

#navbar {
  overflow: hidden;
  background-color: #333;
}

#navbar a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

#navbar a:hover {
  background-color: #ddd;
  color: black;
}

#navbar a.active {
  background-color: #333;
  color: white;
}

.content {
  padding: 16px;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.sticky + .content {
  padding-top: 60px;
}
/* end nav */
/* Authors */
.authors {
  box-sizing: border-box;
}

.flex-container {
  display: flex;
  flex-direction: row;
  font-size: 30px;
  text-align: center;
  margin: 1%;
}

.flex-item-left {
  background-color: #929191;
  padding: 10px;
  flex: 50%;
}

.flex-item-right {
  background-color: #929191;
  padding: 10px;
  flex: 50%;
}

@media (max-width: 800px) {
  .flex-container {
    flex-direction: column;
  }
}
@media (max-width:435px){
    .img{
      width: 200px;
      height: 50px;
    }
}
@media (max-width:220px){
  .img{
    width: 100px;
    height: 25px;
  }
}
.authors-img{
   width: 190.5px;
   height: 249px;
}
.flex-item-right{
  border: solid 5px black;
}
.flex-item-left{
  border: solid 5px black;
}
.btn {
  display: block;
  width: 100%;
  border: none;
  background-color: #4CAF50;
  padding: 14px 28px;
  font-size: 25px;
  cursor: pointer;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
}
/* kitoblar */
/* Center website */
.kitob {
  max-width: 1200px;
  margin: auto;
  margin-top: 1%;
 
}

h1 {
  font-size: 50px;
  word-break: break-all;
}

.row {
  margin: 8px -16px;
}

/* Add padding BETWEEN each column */
.row,
.row > .column {
  padding: 8px;
}

/* Create four equal columns that floats next to each other */
.column {
  float: left;
  width: 25%;
  margin: 1%;
}

/* Clear floats after rows */ 
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Content */
.content {
  background-color: rgb(136, 131, 131);
  padding: 10px;
}

/* Responsive layout - makes a two column-layout instead of four columns */
@media screen and (max-width: 900px) {
  .column {
    width: 50%;
  }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
  }
}
.jpg{
  width:100%;
}
.flex{
   display: flex;
   justify-content: center;
}
@media(max-width:617px){
    .flex{
        margin: 5%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}
/* end authors */

